.possiblity{
    display: flex;
    flex-direction: row;
}

.possiblity-image{
    flex: 1;
    display: grid;
    justify-content: flex-start;
    align-items: center;
    margin-right:2rem ;
}
.possibility-image img{
width: 100%;
    height: 100%;
}
.possiblity-content{
flex: 1;
display: flex;
flex-direction: column;
justify-content: flex-end;
align-items: flex-start;

}
.possibility_content h4{
font-weight: 500;
font-size: 16px;
line-height: 30px;
color: aqua;
}
.possibility_content h4:last-child{
 color: brown;   
}
.possibility_content h1{
font-weight: 800;
font-size: 34px;
line-height: 45px;
margin: 1rem 0;
}
.possibility_content p{
  font-size: 16px;
  line-height: 30px;
  color: rgb(133, 189, 189);
  margin-bottom: 2rem;
}

@media screen and (max-width:950px) {
    .possiblity{
        flex-direction: column;
    }
    .possibility-image{
        margin: 1rem 0;
    }
    .possibility_content{
        margin-top: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .possibility-image img {
        width: 100%;
        height: 100%;
    }
}
