.cta{
   border: 1px solid white;
 
   display: flex;
   align-items: center;
   justify-content: space-between; 
   flex-direction: row;
   padding: 2rem;
   margin: 4rem;
   border-radius: 1rem;background:linear-gradient(45deg,rgb(250, 168, 181) ,rgb(246, 7, 7));
}
.cta-content p{
 font-weight: 500;
 font-size: 24px;
line-height: 45px;
color: black;
}
.cta-content h3{
    font-weight: 500;
    font-size: 24px;
    line-height: 25px;
}
.ctn-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.cta-btn button{
    background-color: black;
    color: aliceblue;
    border-radius: 1.5rem;
    border: none;
    font-size: 18px;
    line-height: 30px;
    padding: .5rem 1rem;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width:650px) {
 .cta{
    flex-direction: column;

 }
 .cta-btn{
    margin: 2rem 0 0;
 }
    
}
  
@media screen and (max-width:550px) {
    .cta {
        flex-direction: column;

    }

    .cta-content h3{
      font-size: 18px;
      line-height: 32px;
    }
    .cta-btn button{
       font-size: 14px; 
        line-height: 28px;
    }

}