.header {
    display: flex;
    flex-direction: row;


}

.header_section_content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.header_section_content h1 {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 800;
    font-size: 63px;
    line-height: 75px;
    letter-spacing: -0.04rem;

}

.header_section_content p {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 20px;
    font-weight: 500;
    line-height: 27px;
    color: aliceblue;
    margin-top: 1.5rem;
}

.header_content_input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}

.header_content_input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: rgb(58, 58, 58);
    border-radius: 5px 0px 0px 5px;
    font-size: 20px;
    line-height: 27px;
    color: rgb(255, 255, 255);
outline: none;
}

.header_content_input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background: red;
    border: none;
    border-radius: 5px 5px 5px 5px;

    font-style: normal;
    font-weight: bold;
    line-height: 27px;
    color: aliceblue;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
}

.header_content_people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
    flex-direction: row;
}

.header_content_people img {
    width: 181px;
    height: 38px;

}

.header_content_people p {
    margin: 0 0 0 1rem;
    font-family: manrope;
    font-style: normal;
    font-size: 12px;
    font-weight: 500;
    line-height: 38px;
    color: aliceblue;
    text-align: center;

}

.header_image_AI {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}

.header_image_AI img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1050px) {
    .header {
       flex-direction: column;
       
    }

    

    .header_section_content {
        margin: 0 0 3rem;}

    }.header_image_AI img {
        width: 100%;
        height: 100%;
    }

    @media screen and (max-width:650px) {
        .header h1 {
            font-size: 48px;
            line-height: 60px;
        }

        .header p {
            font-size: 48px;
            line-height: 60px;
        }

        .header_content_people {
            flex-direction: column;
        }

        .header_content_people p {
            margin: 0;
        }

        .header_content_input,
        .header_content_input input {
            font-size: 16px;
            line-height: 24px;

        }.header_image_AI img {
            width: 100%;
            height: 100%;}
        
        

    }

    @media screen and (max-width:490px) {
        .header h1 {
            font-size: 36px;
            line-height: 48px;
        }

        .header p {
            font-size: 14px;
            line-height: 24px;
        }

        .header_content_people {
            flex-direction: column;
        }

        .header_content_people p {
            margin: 0;
        }

        .header_content_input button,
        .header_content_input input {
            font-size: 12px;
            line-height: 16px;
        }
.header_image_AI img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
    }