.features-container__feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin:1rem;
    
}
.features-container_feature-title{
    flex: 1;
    max-width: 100px;
    margin-right: 2rem;
    color: aliceblue;
}
.features-container_feature-title h1{
  font-family: Manrope;
  font-size: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.04rem;
  color: rgb(247, 249, 250);  
}
.features-container_feature-title div{
width: 38px;
height:3px ;
background: linear-gradient(45deg,red,rgb(125, 125, 255));
}
.features-container_feature-text{
  flex: 2;
  max-width: 390px;
  display: flex;
}
.features-container_feature-text p{
   font-weight: 500;

  color: antiquewhite;

   size: 14px;
}
@media screen and (max-width:550px) {
  .features-container_feature-title h1{
    font-size: 14px;
    line-height: 22px;

  }.features-container_feature-title p{
    font-size: 12px;
    line-height: 20px;
  
  }
  .features-container_feature{
margin: 1rem 0;

  }
}