.footer{
    border: 1px solid rgb(242, 239, 234);
    color: aliceblue;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    

}
.footer-heading{
    width: 100%;
    text-align: center;
}
.footer-heading h1{
    font-weight: 800;
    font-size: 83px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04rem;
}

.footer-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid white;
    text-align: center;
    margin: 2rem 0rem 10rem 0rem;
    cursor: pointer;
}
.footer-btn p{
      box-sizing: border-box;
    font-size: 18px;
    line-height:21px;
    word-spacing: 4px;

}

.footer-links{
 display: flex;
 justify-content: space-between;
 align-items: flex-start;
 flex-wrap: wrap;
 flex-direction: row;
 width: 100%;
 text-align: left;
}

.footer-links div{

width: 250px;
margin: 1rem;
}
.footer-links_logo{
    display: flex;
    flex-direction: column;
}
.footer-links_logo img{
    width: 118px;
    height: 30px;
}
.footer-links_logo p{
    font-size: 12px;
    line-height: 14px;
}
.footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

}
.footer-links_div h4{
    font-size: 12px;
    line-height: 14px;
    color: aliceblue;
    margin: .5rem 0;
    cursor: pointer;
}

.footer-copyright{
     margin-top: 2rem;
     text-align: center;
     width: 100%;
}
.footer-copyright p{
    font-size: 12px;
    line-height: 14px;
    font-feature-settings:'liga'off ;
    color:cornsilk;
}
@media screen and(max-width:850px) {
    .footer-heading h1{
        font-size: 44px;
        line-height: 50px;
    }
}
@media screen and(max-width:550px) {
    .footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
    .footer-copyright div{
        margin: 1rem 0;
    }
    .footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
}
